#work {
  height: 100%;
  background-color: #EBE2DD;
  color: #57616C;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.mask { 
  margin-top: 25px;
  overflow:hidden; height:25px; 
}

.mask::after { 
  content: ''; 
  display: block;
  margin: -25px auto 0;
  width: 30em;
  height: 25px;  
  border-radius: 125px / 12px;  
  -webkit-box-shadow: 0 0 8px black;  
          box-shadow: 0 0 8px black;
}



